import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Heading } from "@/components/ui/heading";
import { Input } from "@/components/ui/input";
import { Checkbox } from "@/components/ui/checkbox";
import { Text } from "@/components/ui/text";

export default function Login() {
  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-white">
      {/* 顶部切换按钮 */}
      <div className="flex mb-4">
        <Button variant="ghost" className="px-8 py-2">
          登录账号
        </Button>
        <Button variant="ghost" className="px-8 py-2">
          注册账号
        </Button>
      </div>
      {/* 登录表单 */}
      <Card className="flex flex-col items-start p-4 space-y-2 w-96">
        <Heading as="h2" size="lg">
          登录账号
        </Heading>
        <Text className="text-gray-500 text-sm">
          欢迎回来
        </Text>
        <div className="flex flex-col space-y-2 w-full">
          <label htmlFor="phone" className="text-sm">
            手机号
          </label>
          <Input
            id="phone"
            placeholder="请输入手机号"
            className="w-full"
          />
          <label htmlFor="password" className="text-sm">
            密码
          </label>
          <Input
            id="password"
            placeholder="请输入密码"
            type="password"
            className="w-full"
          />
          <div className="flex justify-between items-center">
            <Checkbox>
              记住登录
            </Checkbox>
            <Text as="a" href="#" className="text-gray-500 text-sm underline">
              忘记密码?
            </Text>
          </div>
          <Button variant="solid" className="px-8 py-2 w-full">
            登录
          </Button>
        </div>
      </Card>
    </div>
  );
}